class Day extends React.Component{
    constructor(props) {
      super(props);
  
    }

    renderData() {
        let children = []
        for (var property in this.props.data) {
            if (this.props.data.hasOwnProperty(property)) {
              // Do things here
              children.push(<p> Hello</p>)
            }
          }
    }

    render() {
      return (
          <div>
        {/* <p>{JSON.stringify(this.props.data, null, 2)} </p> */}
        {this.renderData()}
        </div>
      )
    }
  }

  export default Day